"use client";

import { useEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import Navbar from "./components/Navbar";

export default function HomePage() {
  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    gsap.utils.toArray("#stats .stat-number").forEach(num => {
      const value = parseFloat(num.dataset.value);
      gsap.fromTo({ val: 0 }, { val: value }, {
        duration: 1.5,
        scrollTrigger: { trigger: num, start: "top 80%" },
        onUpdate() {
          num.innerHTML = Math.round(this.targets()[0].val) + (num.innerHTML.includes('%') ? '%' : '+');
        }
      });
    });
  }, []);

  return (
    <>
      <Navbar />

      {/* HERO */}
      <section className="hero">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6">
              <h1>Intelligent Trade Document Verification Using AI</h1>
              <p className="lead">
                Instantly validate invoices, bills of lading, packing lists,
                certificates of origin, and customs documents using AI.
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* STATS */}
      <section id="stats" className="section bg-light">
        <div className="container">
          <div className="row g-4">
            <div className="col-md-3">
              <div className="stat-number" data-value="99">0%</div>
              <div className="stat-label">Accuracy</div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
